<template>
	<!-- 新版九宫格智能处理 -->
	<view class="imgBox">
		<view :class="imageListClass" v-for="(img, index) in imageList" :key="index">
			<view class="bgsize"> <image class="img" :src="img" mode="aspectFill" @tap="previewImage(img)"></image>
			<image class="guanbi" src="/static/img/guanbi.svg" mode="" @tap="delImage(index)"></image>
			<!-- <view class="icon iconfont icon-guanbi1" @tap="delImage(index)"></view> -->
			</view>
		</view>
	</view>
</template>

<script>
export default {
	props: {
		imageList: {
			type: Array,
			default() {
				return [];
			}
		}
	},
	data() {
		return {
			imageListClass: ''
		};
	},
	watch: {
		imageList(newValue, oldValue) {
			let addClass = '';
			console.log('this.imageList.length',this.imageList.length);
			switch (this.imageList.length) {
				case 1:
					addClass = 'one';
					break;
				case 2:
				case 4:
					addClass = 'two';
					break;
				case 3:
				case 6:
				case 9:
					addClass = 'three';
					break;
				case 5:
				case 8:
					addClass = 'five';
					break;
				case 7:
					addClass = 'seven';
					break;
				default:
				console.log('默认');
					break;
			}
			console.log('imageListClass ',addClass);
			this.imageListClass = addClass;
		}
	},
	methods: {
		previewImage(img) {
			this.$emit('previewImage', { src: img });
		},
		delImage(index) {
			this.$emit('delImage', { index: index });
		}
	}
};
</script>

<style lang="scss" scoped>
/* 图片列表布局 */
.imgBox {
	margin-bottom: 20rpx 0;
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	
	// 1张 1,2张 1-1, 3张 1-1-1, 4张 2-2,5张2-3, 6张 3-3, 7张2-2-3,8张 2-3-3, 9张 3-3-3
	
	.one {
		width: 60%;
		height: 480rpx;
	}
	.two , .five:nth-of-type(-n + 2),.seven:nth-of-type(-n + 4) {
		width: calc(98% / 2); /* calc里面的运算符两边要空格 */
		height: 350rpx;
	}
	.three, .five:nth-of-type(n + 3),.seven:nth-of-type(n + 5){
		width: calc(98% / 3); /* calc里面的运算符两边要空格 */
		height: 228rpx;
	}

	.bgsize {
		width: 100%;
		height: 100%;
		background-repeat: no-repeat;
		background-position-x: center;
		background-position-y: center;
		background-size: contain;
		position: relative;

		.img {
			width: 100%;
			height: inherit;
			background-color: #ffffff;
		}

		.guanbi {
			position: absolute;
			right: 10rpx;
			top: 10rpx;
			width: 40rpx;
			height: 40rpx;
			border-radius: 50%;
			overflow: hidden;
			border: 0;
			z-index: 100;
			background-color: #B2B2B2;
		}
	}
}
</style>
